<template>
  <div class="page">
    <div class="content list-editor">
      <h2>Vizsgajegyek</h2>
      <form action="">
        <b-table
          thead-class="hidden_header"
          id="table"
          class="table-content"
          table-variant="light"
          :fields="fields"
          :items="this.list"
          :per-page="perPage"
          :current-page="currentPage"
          ref="table"
        >
          <template #cell(examGrade)="data">
            {{ data.item.examGrade
            }}<b-icon-trash-fill
              class="floated-right"
              @click="Remove(data.index, data.item.examGrade)"
            ></b-icon-trash-fill>
          </template>
        </b-table>
        <b-pagination
          variant="dark"
          class="pagination-place"
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <span>{{ errorMessage }}</span>
        <h3>Új vizsga jegy hozzáadása</h3>
        <div class="inline-elements">
          <input
            placeholder="vizsgajegy"
            type="text"
            class="list-editor-input"
            v-model="element"
            v-on:keyup.enter="Add"
          />
          <button class="login-button add-button" @click="Add">
            vizsgajegy hozzáadása
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "ExamGradeListEditor",

  data() {
    return {
      fields: [{ key: "examGrade", label: "Vizsga jegy", sortable: true }],
      list: [
        { examGrade: "Elégtelen" },
        { examGrade: "Elégséges" },
        { examGrade: "Megfelelt" },
        { examGrade: "Jó" },
        { examGrade: "Kiváló" },
      ],
      perPage: 10,
      currentPage: 1,
      errorMessage: "",
      element: "",
    };
  },
  computed: {
    rows() {
      return this.list.length;
    },
  },
  methods: {
    Add() {
      if (this.element != "") {
        this.element = this.element.trim();
        this.institutions.push({ examGrade: this.element });
        this.$refs.table.refresh();
        this.element = "";
      }
    },
    Edit() {},
    Remove(index, element) {
      if (confirm("Biztos törli a(z) " + element + " vizsga jegyet?")) {
        this.$delete(this.list, index);
      }
      this.$refs.table.refresh();
    },
  },
};
</script>
<style>
.add-button {
  height: 50px;
}
</style>
