<template>
  <div class="page">
    <div class="content list-editor">
      <h2>Képzések</h2>
      <form action="">
        <b-table
          thead-class="hidden_header"
          id="table"
          class="table-content"
          table-variant="light"
          :fields="fields"
          :items="this.list"
          :per-page="perPage"
          :current-page="currentPage"
          ref="table"
        >
          <template #cell(qualification)="data">
            {{ data.item.qualification
            }}<b-icon-trash-fill
              class="floated-right"
              @click="Remove(data.index, data.item.qualification)"
            ></b-icon-trash-fill>
          </template>
        </b-table>
        <b-pagination
          variant="dark"
          class="pagination-place"
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <span>{{ errorMessage }}</span>
        <h3>Új képzés hozzáadása</h3>
        <div class="inline-elements">
          <input
            type="text"
            class="list-editor-input"
            v-model="element"
            v-on:keyup.enter="Add"
            placeholder="képzés"
          />
          <button class="login-button add-button" @click="Add">
            képzés hozzáadása
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "QualificationListEditor",

  data() {
    return {
      fields: [{ key: "qualification", label: "Képzés neve", sortable: true }],
      list: [
        { qualification: "Általános" },
        { qualification: "Hat éves" },
        { qualification: "Spec angol" },
        { qualification: "Német-Angol" },
        { qualification: "Informatikai alap képzés" },
      ],
      perPage: 10,
      currentPage: 1,
      errorMessage: "",
      element: "",
    };
  },
  computed: {
    rows() {
      return this.list.length;
    },
  },
  methods: {
    Add() {
      if (this.element != "") {
        this.element = this.element.trim();
        this.institutions.push({ qualification: this.element });
        this.$refs.table.refresh();
        this.element = "";
      }
    },
    Edit() {},
    Remove(index, element) {
      if (confirm("Biztos törli a(z) " + element + " képzést?")) {
        this.$delete(this.list, index);
      }
      this.$refs.table.refresh();
    },
  },
};
</script>
