<template>
  <div class="page">
    <div class="content list-editor">
      <h2>Vizsgáztatók</h2>
      <form action="">
        <b-table
          thead-class="hidden_header"
          id="table"
          class="table-content"
          table-variant="light"
          :fields="fields"
          :items="this.list"
          :per-page="perPage"
          :current-page="currentPage"
          ref="table"
        >
          <template #cell(teacher)="data">
            {{ data.item.teacher
            }}<b-icon-trash-fill
              class="floated-right"
              @click="Remove(data.index, data.item.teacher)"
            ></b-icon-trash-fill>
          </template>
        </b-table>
        <b-pagination
          variant="dark"
          class="pagination-place"
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <span>{{ errorMessage }}</span>
        <h3>Új vizsgáztató hozzáadása</h3>
        <div class="inline-elements">
          <input
            placeholder="vezetéknév"
            type="text"
            class="list-editor-input"
            v-model="surName"
          />
          <input
            placeholder="Keresztnév"
            type="text"
            class="list-editor-input"
            v-model="lastName"
            v-on:keyup.enter="Add"
          />
          <button class="login-button add-button" @click="Add">
            vizsgáztató hozzáadása
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "QualificationListEditor",

  data() {
    return {
      fields: [{ key: "teacher", label: "Tanár", sortable: true }],
      list: [
        { teacher: "Kiss Béla" },
        { teacher: "Kovács József" },
        { teacher: "Boglár Klára" },
        { teacher: "Tóth József" },
        { teacher: "Ács Tamás" },
      ],
      perPage: 10,
      currentPage: 1,
      errorMessage: "",
      surName: "",
      lastName: "",
      element: "",
    };
  },
  computed: {
    rows() {
      return this.list.length;
    },
  },
  methods: {
    Add() {
      if (this.surName != "" && this.lastName) {
        this.surName = this.surName.trim();
        this.lastName = this.lastName.trim();
        this.list.push({ teacher: this.surName + " " + this.lastName });
        this.$refs.table.refresh();
        this.surName = "";
        this.lastName = "";
      }
    },
    Edit() {},
    Remove(index, element) {
      if (confirm("Biztos törli a(z) " + element + " vizsgáztatót?")) {
        this.$delete(this.list, index);
      }
      this.$refs.table.refresh();
    },
  },
};
</script>
