<template>
  <div class="page">
    <div class="content content-better-name floated-right">
      <h2>Hash kód létrehozása (Vizsgák)</h2>

      <div>
        <b-form-input
          class="input-field-for-search"
          id="filter-input"
          v-model="filter"
          type="search"
          placeholder="Keresés Hallgató neve alapján"
        ></b-form-input>
        <b-table
          striped
          id="table"
          table-variant="light"
          sort-icon-left
          selectable
          @row-selected="onRowSelected"
          :fields="fields"
          :items="items"
          :per-page="perPage"
          :current-page="currentPage"
        >
          <template #cell(selected)="{ rowSelected }">
            <template v-if="rowSelected">
              <span class="check-mark" aria-hidden="true">&check;</span>
              <span class="sr-only">Selected</span>
            </template>
            <template v-else>
              <span aria-hidden="true">&nbsp;</span>
              <span class="sr-only">Not selected</span>
            </template>
          </template>

          <template #cell(show_details)="row">
            <b-button size="sm" @click="row.toggleDetails" class="mr-2">
              Részletek {{ row.detailsShowing ? "Elrejtése" : "Mutatása" }}
            </b-button>
          </template>
          //TODO adatok megváltoztatása a hash alapján
          <template #row-details="row">
            <b-card>
              <b-row class="mb-2">
