<template>
  <div class="page">
    <div class="content content-better-name floated-right">
      <h2>Hash kód létrehozása (Tanúsítványok)</h2>
      <div>
        <b-form-input
          class="input-field-for-search"
          id="filter-input"
          v-model="filter"
          type="search"
          placeholder="Keresés Hallgató neve alapján"
        ></b-form-input>
        <b-table
          striped
          id="table"
          table-variant="light"
          selectable
          @row-selected="onRowSelected"
          :fields="fields"
          :items="items"
          :per-page="perPage"
          :current-page="currentPage"
        >
          <template #thead-top="">
            <b-tr>
              <b-th colspan="1"><span class="sr-only"></span></b-th>
              <b-th variant="secondary" colspan="3">Képző</b-th>
              <b-th variant="light" colspan="2">Képzett</b-th>
              <b-th variant="secondary" colspan="3">Vizsga</b-th>
            </b-tr>
          </template>

          <template #cell(selected)="{ rowSelected }">
            <template v-if="rowSelected">
              <span class="check-mark" aria-hidden="true">&check;</span>
              <span class="sr-only">Selected</span>
            </template>
            <template v-else>
              <span aria-hidden="true">&nbsp;</span>
              <span class="sr-only">Not selected</span>
            </template>
          </template>

          <template #cell(show_details)="row">
            <b-button size="sm" @click="row.toggleDetails" class="mr-2">
              Részletek {{ row.detailsShowing ? "Elrejtése" : "Mutatása" }}
            </b-button>
          </template>
          //TODO adatok megváltoztatása a hash alapján
          <template #row-details="row">
            <b-card>
              <b-row class="mb-2">
                <b-col sm="2" class="text-sm-right"><b>Hash kód</b></b-col>
                <b-col sm="2" class="text-sm-right"><b>Hitelesített</b></b-col>
                <b-col sm="2" class="text-sm-right"
                  ><b>Hitelesítés dátuma</b></b-col
                >
                <b-col sm="2" class="text-sm-right"
                  ><b>Hash ellenőrzött</b></b-col
                >
                <b-col sm="2" class="text-sm-right"
                  ><b>Hash ellenörzés dátuma</b></b-col
                >
              </b-row>

              <b-row class="mb-2">
                <b-col>{{ row.item.egy }}</b-col
                ><b-col>{{ row.item.ketto ? "&check;" : "&#10060;" }}</b-col>
                <b-col>{{ row.item.harom }}</b-col>
                <b-col>{{ row.item.negy ? "&check;" : "&#10060;" }}</b-col>
                <b-col>{{ row.item.ot }}</b-col>
              </b-row>
            </b-card>
          </template>

          <template #cell(hash)="data"
            >{{ data.item.certificationSigned }}
          </template>
          <template #cell(institution)="data">{{
            data.item.institution
          }}</template>

          <template #cell(qualification)="data"
            >{{ data.item.qualification }}
          </template>

          <template #cell(certificationId)="data"
            >{{ data.item.certificationId }}
          </template>
          <template #cell(participantName)="data">
            {{ data.item.participantName }}</template
          >
          <template #cell(participantBirth)="data">
            {{ data.item.participantBirth }}</template
          >
          <template #cell(qualificationDate)="data"
            >{{ data.item.qualificationDate }}
          </template>
          <template #cell(certificationGrade)="data">
            {{ data.item.certificationGrade }}</template
          >
          <template #cell(certificationDate)="data"
            >{{ data.item.certificationDate }}
          </template>
          <template #cell(certificationSigned)="data"
            >{{ data.item.certificationSigned }}
          </template>
        </b-table>

        <b-pagination
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <button class="login-button" id="login-button-style">
          Hash létrehozása
        </button>
        <span>{{ errorMessage }}</span>
      </div>
    </div>
  </div>
</template>

<script>
//import axios from "axios";
// Bootstrap Vue használatával
export default {
  name: "ListCertifications",

  data() {
    return {
      fields: [
        "show_details",
        "selected",
        {
          key: "certificationId",
          label: "Tanusítvány sorszáma",
          sortable: true,
        },
        { key: "institution", label: "Intézmény neve", sortable: true },
        { key: "qualification", label: "Képzés neve", sortable: true },
        {
          key: "qualificationDate",
          label: "Képzés időtartama",
          sortable: true,
        },
        { key: "participantName", label: "Képzett neve", sortable: true },
        { key: "participantBirth", label: "Születési dátuma", sortable: true },
        {
          key: "certificationGrade",
          label: "Képzés minősítése",
          sortable: true,
        },
        {
          key: "certificationDate",
          label: "Tanúsítvány kiállításának dátuma",
          sortable: true,
        },
        {
          key: "certificationSigned",
          label: "Tanúsítványt aláírók nevei",
          sortable: true,
        },
      ],
      certifications: [
        {
          egy: "0x0000000000",
          ketto: true,
          harom: "2021-01-03",
          negy: false,
          ot: "2021-01-04",
          institution: "Avasi Gimnázium",
          qualification: "Hat osztályos",
          certificationId: "01",
          participantName: "Tóth Gergő",
          participantBirth: "1997-12-23",
          qualificationDate: "2010-2016",
          certificationGrade: "Kiváló",
          certificationDate: "2016-06-12",
          certificationSigned: "Arday István",
        },
        {
          egy: "",
          ketto: false,
          harom: "",
          negy: false,
          ot: "",
          institution: "Avasi Gimnázium",
          qualification: "Angol Német kéttan nyelvű",
          certificationId: "02",
          participantName: "Horvát Szabolcs",
          participantBirth: "1997-10-23",
          qualificationDate: "2010-2014",
          certificationGrade: "Elégséges",
          certificationDate: "2014-06-12",
          certificationSigned: "Arday István",
        },
      ],
      filter: null,
      perPage: 15,
      currentPage: 1,
      errorMessage: "",
    };
  },
  created() {}, // function () {
  //   axios({
  //     method: "GET",
  //     //TODO: change URL where the data came from
  //     url: "/users",
  //     data: "",
  //     headers: {
  //       "content-type": "application/json",
  //     },
  //   })
  //     .then((response) => {
  //       this.certifications = response.data;
  //       console.log(this.certifications);
  //     })
  //     .catch((error) => {
  //       switch (error.response.status) {
  //         case 401:
  //           this.errorMessage = "Érvénytelen felhasználónév vagy jelszó!";
  //           alert(this.errorMessage);
  //           break;
  //         case 404:
  //           this.errorMessage = "A weboldal nem érhető el.";
  //           alert(this.errorMessage);
  //           break;
  //         case 504:
  //           this.errorMessage = "Nincs szerver kapcsolat.";
  //           alert(this.errorMessage);
  //           break;
  //         default:
  //           this.errorMessage = "Valami hiba történt.";
  //           alert(this.errorMessage);
  //       }
  //     });
  // },
  computed: {
    rows() {
      return this.certifications.length;
    },
    items() {
      return this.filter
        ? this.certifications.filter((item) =>
            item.participantName.includes(this.filter)
          )
        : this.certifications;
    },
  },
  methods: {
    onRowSelected(items) {
      this.selected = items;
    },
  },
};
</script>

<style></style>
