<template>
  <div class="login-box">
    <h2>Bejelentkezés</h2>
    <form class="input-form">
      <input
        class="login-input"
        type="text"
        v-model="user.username"
        id="username"
        placeholder="Felhasználónév:"
      />

      <input
        class="login-input"
        type="password"
        id="password"
        v-model="user.password"
        v-on:keyup.enter="signIn"
        placeholder="Jelszó:"
      />
      <button class="login-button" id="login-button-style" v-on:click="signIn">
        Bejelentkezés
      </button>
      <div>
        <b-link to="/registration">Regisztráció</b-link>
      </div>
    </form>
  </div>
</template>

<script>
//import "./assets/styles/styles.css";
import axios from "axios";
export default {
  name: "Login",
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
      errorMessage: "",
    };
  },
  methods: {
    signIn() {
      axios
        .post("/api/login", {
          username: this.user.username,
          password: this.user.password,
        })
        .then(() => {
          this.$router.push("profile");
        })
        .catch((error) => {
          switch (error.response.status) {
            case 401:
              this.errorMessage = "Érvénytelen felhasználónév vagy jelszó!";
              alert(this.errorMessage);
              break;
            case 404:
              this.errorMessage = "A weboldal nem elérhető";
              alert(this.errorMessage);
              break;
            case 504:
              this.errorMessage = "Nincs szerver kapcsolat";
              alert(this.errorMessage);
              break;
            default:
              this.errorMessage = "Valami hiba történt...";
              alert(this.errorMessage);
          }
        });
      this.$router.push("home");
    },
  },
};
</script>
<style>
.login-box {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  padding: 40px;
  transform: translate(-50%, -50%);
  background: #ffffff;
  box-sizing: border-box;
  box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}
h2 {
  color: #373378;
  margin-bottom: 40px;
}
.login-input {
  font-size: 16px;
  display: block;
  margin: 20px auto;
  margin-bottom: 40px;
  border: none;
  border-bottom: 2px solid #373378;
  background-color: transparent;
  color: #777;
}
.input-form {
  margin: auto;
  display: inline-block;
}
.login-input ::placeholder {
  color: #03e9f4;
}
.login-button {
  height: 40px;
  margin: 20px;
  padding: 10px, 20px;
  font-size: 16px;
  border: none;
  color: #fff;
  background: #373378;

  border: 2px solid #373378;
  border-radius: 5px;
  transition: 0.5;
  letter-spacing: 3px;
  font-weight: 600;
  text-transform: uppercase;
}
.login-button:hover {
  border: none;
  border-radius: 5px;
  background: #fff;
  color: #373378;

  border: 2px solid #373378;
}
.input-form input {
  margin: auto;
  margin-bottom: 15px;
}
.login-input::active {
  text-decoration: none;
  border: none;
}
.table-content {
  margin: auto;
  align-self: center;
}
.list-editor {
  margin-left: 400px;
  width: 400px;
  height: auto;
}
.hidden_header {
  display: none;
}
.page-link {
  background-color: #99cf17 !important;
  color: #373378 !important;
  border-color: #99cf17 !important;
}
h3 {
  margin-bottom: 40px;
}
.page-item.active .page-link {
  z-index: 3;
  color: #99cf17 !important;
  background-color: #373378 !important;
  border-color: #373378 !important;
}
.list-editor-input {
  margin: auto;
  display: inline;
  margin-bottom: 15px;
  width: 200px;
  height: 32px;
}
.plus-icon {
  color: #373378 !important;
  margin-top: 10px;
}
.b-icon.bi {
  vertical-align: 0;
}
.inline-elements {
  align-items: flex-end;
}

span.sr-only {
  display: none !important;
}
link {
  text-decoration: none;
}
</style>
