<template>
  <div class="page">
    <div class="content list-editor">
      <h2>Intézmények</h2>
      <form action="">
        <b-table
          thead-class="hidden_header"
          id="table"
          class="table-content"
          table-variant="light"
          :fields="fields"
          :items="this.institutions"
          :per-page="perPage"
          :current-page="currentPage"
          ref="table"
        >
          <template #cell(institution)="data">
            {{ data.item.institution }}

            <b-icon-trash-fill
              class="floated-right"
              @click="Remove(data.index, data.item.institution)"
            ></b-icon-trash-fill>
          </template>
        </b-table>
        <b-pagination
          variant="dark"
          class="pagination-place"
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <span>{{ errorMessage }}</span>
        <h3>Új intézmény hozzáadása</h3>
        <div class="inline-elements">
          <input
            placeholder="intézmény"
            type="text"
            class="list-editor-input"
            v-model="element"
            v-on:keyup.enter="Add"
          />
          <button class="login-button add-button" @click="Add">
            Intézmény hozzáadása
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "InstitutionListEditor",
  data() {
    return {
      fields: [{ key: "institution", label: "Intézmények", sortable: true }],
      institutions: [
        { institution: "Kandó" },
        { institution: "Avasi" },
        { institution: "Földes" },
        { institution: "Herman" },
        { institution: "Bláthy" },
        { institution: "Andrássy" },
        { institution: "Kandó" },
        { institution: "Avasi" },
        { institution: "Földes" },
        { institution: "Herman" },
        { institution: "Bláthy" },
        { institution: "Andrássy" },
        { institution: "Kandó" },
        { institution: "Avasi" },
        { institution: "Földes" },
        { institution: "Herman" },
        { institution: "Bláthy" },
        { institution: "Andrássy" },
      ],
      perPage: 10,
      currentPage: 1,
      errorMessage: "",
      element: "",
    };
  },
  computed: {
    rows() {
      return this.institutions.length;
    },
  },
  methods: {
    Add() {
      if (this.element != "") {
        this.element = this.element.trim();
        this.institutions.push({ institution: this.element });
        this.$refs.table.refresh();
        this.element = "";
      }
    },
    Edit() {},
    Remove(index, institution) {
      if (confirm("Biztos törli a(z) " + institution + " intézményt?")) {
        this.$delete(this.institutions, index);
      }
      this.$refs.table.refresh();
    },
  },
};
</script>

<style></style>
